<template>
  <h1>Products</h1>
  <ProductsList :products="products" />
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
// import productData from "@/data/temp-data";
import axios from 'axios';
import ProductsList from "@/components/ProductsList.vue";
import IProduct from "@/types/IProduct";

export default defineComponent({
  name: "ProductsPage",
  components: {
    ProductsList,
  },
  setup() {
    const products = ref<IProduct[]>([]); // Reactive variable to store the products

    const fetchProducts = async () => {
      try {
        const response = await axios.get<IProduct[]>("/api/products");
        products.value = response.data; // Update the reactive variable
      } catch (error) {
        console.error("Error fetching products:", error);
      }
    };

    onMounted(() => {
      fetchProducts(); // Call the function when the component is mounted
    });
    
    return {
      products, // Expose products as part of the return object
    };
  },
});
</script>
