<template>
    <div v-if="product">
        <div class="img-wrap">
            <img :src="product.imageUrl" alt="" />
        </div>
        <div class="product-details">
            <h1>{{ product.name }}</h1>
            <h3 class="price">{{ product.price }}</h3>
            <button @click="addToCart" class="add-to-cart" v-if="user && !itemIsInCart">
                Add to cart
            </button>
            <button class="grey-button" v-if="user && itemIsInCart">
                Item is already in cart
            </button>
            <button class="sign-in" @click="openEmailModal" v-if="!user">Sign in to add to cart</button>
        </div>
        <!-- two-way: -->
        <!-- <MessageModal
      v-model:isVisible="isMessageVisible"
      :title="modalTitle"
      :message="modalMessage"
    /> -->

        <!-- one-way: -->
        <MessageModal
            v-show="isMessageModalVisible"
            @close="isMessageModalVisible = false"
            :title="modalTitle"
            :message="modalMessage" />

        <EmailModal
            v-show="isEmailModalVisible"
            @update-email="handleUpdateSubmit"
            @close="isEmailModalVisible = false" />
    </div>
    <div v-else>
        <NotFoundPage />
    </div>
</template>

<script lang="ts">
// import productData from "@/data/temp-data";
import
{
    getAuth,
    sendSignInLinkToEmail,
    isSignInWithEmailLink,
    signInWithEmailLink,
} from "firebase/auth";
import { computed, defineComponent, onMounted, PropType, ref } from "vue";
import { useRoute } from "vue-router";
import axios from "axios";
import IProduct from "@/types/IProduct";
import EmailModal from "@/modals/EmailModal.vue";
import IUser from "@/types/IUser";
import NotFoundPage from "./NotFoundPage.vue";
import MessageModal from "../modals/MessageModal.vue";

export default defineComponent({
    name: "ProductDetailPage",
    components: {
        NotFoundPage,
        MessageModal,
        EmailModal,
    },
    props: {
        user: {
            type: Object as PropType<IUser>,
            required: true,
        }
    },
    setup(props)
    {
        const route = useRoute(); // Access route params using useRoute
        // const products = ref(productData); // Wrap product data with ref for reactivity
        const product = ref<IProduct>(); // Reactive variable to store the products
        const cartItems = ref<IProduct[]>([]);
        const isMessageModalVisible = ref(false);
        const isEmailModalVisible = ref(false);
        const modalTitle = "Product Message";
        const modalMessage = ref<string>("");
        const userEmail = ref<string>("");

        const showMessage = (message: string) =>
        {
            modalMessage.value = message;
            isMessageModalVisible.value = true;
        };

        const fetchProducts = async () =>
        {
            try
            {
                const response = await axios.get<IProduct>(
                    `/api/products/${route.params.productId}`
                );
                product.value = response.data; // Update the reactive variable

                const cartResponse = await axios.get(`/api/users/${props.user.uid}/cart`);
                const cartItemData = cartResponse.data;
                cartItems.value = cartItemData;
            } catch (error)
            {
                console.error("Error fetching products:", error);
            }
        };

        const addToCart = async () =>
        {
            await axios.post(`/api/users/${props.user.uid}/cart`, { id: route.params.productId });
            showMessage(
                `Successfully added item (${route.params.productId}) to cart!`
            );
        };

        const itemIsInCart = computed(() =>
            cartItems.value.some((item) => item.id === route.params.productId)
        );

        const processSignIn = async () =>
        {
            const auth = getAuth();
            const actionCodeSettings = {
                url: `http://localhost:8080/products/${route.params.productId}`,
                handleCodeInApp: true,
            };
            await sendSignInLinkToEmail(auth, userEmail.value, actionCodeSettings);
            showMessage("A login link was sent to the email you provided");
            window.localStorage.setItem("emailForSignIn", userEmail.value);
            isEmailModalVisible.value = false;
        };

        onMounted(async () =>
        {
            const auth = getAuth();

            if (isSignInWithEmailLink(auth, window.location.href))
            {
                const email = window.localStorage.getItem('emailForSignIn') ?? '';
                await signInWithEmailLink(auth, email, window.location.href);
                showMessage("Successfully signed in!");
                window.localStorage.removeItem("emailForSignIn");
            }

            fetchProducts(); // Call the function when the component is mounted

            if (props.user) {
                const cartResponse = await axios.get(`/api/users/${props.user.uid}/cart`);
                const cartItems = cartResponse.data;
                cartItems.value = cartItems;
            }
        });

        // Handle the email submit event from the child modal
        const handleUpdateSubmit = (submittedEmail: string) =>
        {
            userEmail.value = submittedEmail;
            isEmailModalVisible.value = false; // Close the modal
            processSignIn();
        };

        const openEmailModal = () =>
        {
            isEmailModalVisible.value = true;
        };

        return {
            //   product: products.value.find(
            //     (product) => product.id === route.params.productId,
            //   ),
            product,
            addToCart,
            itemIsInCart,
            modalTitle,
            modalMessage,
            handleUpdateSubmit,
            openEmailModal,
            userEmail,
            isEmailModalVisible,
            isMessageModalVisible,
        };
    },
});
</script>
