<template>
    <div id="emailModal" class="modal">
        <div class="modal-content">
            <div class="modal-header">
                <h2>Log In</h2>
                <button class="close-btn" @click="$emit('close')">×</button>
            </div>
            <hr />
            <div class="modal-body">
                <p>Please enter your email to sign in:</p>
                <input type="email" id="emailInput" placeholder="Enter your email" required aria-label="email"
                    name="email" @input="handleInput" />
            </div>
            <div class="modal-footer">
                <button id="submitEmail" @click="submitEmail" class="footer-btn">Submit</button>
                <button @click="$emit('close')" class="footer-btn">Cancel</button>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';

export default defineComponent({
    name: 'EmailModal',
    emits: ['update-email', 'close'],
    setup(_, { emit })
    {
        const email = ref('');

        const submitEmail = () =>
        {
            emit('update-email', email.value);
            // emit('close');
        };

        const handleInput = (event: Event) =>
        {
            const newEmail = (event.target as HTMLInputElement).value;
            email.value = newEmail;
        };

        return { email, submitEmail, handleInput };
    },
});
</script>

<style scoped>
/* Modal overlay */
.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
}

/* Modal content */
.modal-content {
    background-color: #fff;
    border-radius: 8px;
    width: 400px;
    max-width: 90%;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 0;
}

.modal-header h2 {
    margin: 0;
    font-size: 1.5em;
}

.modal-header .close-btn {
    background: none;
    border: none;
    font-size: 1.5em;
    font-weight: bold;
    cursor: pointer;
    color: #333;
    padding: 0;
}

.modal-header .close-btn:hover {
    color: #ff0000;
}

hr {
    border: 0;
    border-top: 1px solid #ccc;
    margin: 15px 0;
}

.modal-body {
    font-size: 1em;
    color: #333;
    margin-bottom: 20px;
}

.modal-footer {
    text-align: right;
}

.footer-btn {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 15px;
    margin-left: 15px;
}

.footer-btn:hover {
    background-color: #45a049;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
</style>
