<template>
  <div class="product-container" v-for="product in props.products" :key="product.id">
    <img class="product-image" :src="product.imageUrl" alt="" />
    <div class="details-wrap">
      <h3>{{ product.name }}</h3>
      <p>{{ product.price }}</p>
    </div>
    <!-- <button class="remove-button" @click="$emit('RemoveFromCart', product.id)">Remove from Cart</button> -->
    <button class="remove-button" @click="RemoveFromCart(product.id)">Remove from Cart</button>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import IProduct from "@/types/IProduct";

export default defineComponent({
  name: "ShoppingCartList",
  props: {
    products: {
      type: Array as PropType<IProduct[]>,
      required: true,
    },
  },
  emits: ["remove-from-cart"],
  setup(props, { emit }) {
    const RemoveFromCart = (productId: string) => {
      emit("remove-from-cart", productId);
    };

    return {
      props,
      RemoveFromCart,
    };
  },
});
</script>
