<template>
    <h1>Shopping Cart</h1>
    <div v-if="cartItems.length > 0">
        <ShoppingCartList @remove-from-cart="removeFromCart($event)" :products="cartItems" />
        <button class="checkout-button">Proceed to Checkout</button>
        <!-- one-way: -->
        <MessageModal v-show="isMessageVisible" @close="isMessageVisible = false" :title="modalTitle"
            :message="modalMessage" />
    </div>
    <div v-if="cartItems.length === 0">
        You current have no items in your cart!
    </div>
</template>

<script lang="ts">
import ShoppingCartList from "@/components/ShoppingCartList.vue";
// import { cartItems as cartItemsData } from "@/data/temp-data";
import { defineComponent, onMounted, PropType, ref } from "vue";
import axios from "axios";
import IProduct from "@/types/IProduct";
import IUser from "@/types/IUser";
import MessageModal from "../modals/MessageModal.vue";

export default defineComponent({
    name: "ShoppingCartPage",
    components: {
        ShoppingCartList,
        MessageModal,
    },
    props: {
        user: {
            type: Object as PropType<IUser>,
            required: true,
        }
    },
    setup(props)
    {
        const cartItems = ref<IProduct[]>([]);
        const isMessageVisible = ref(false);
        const modalTitle = "Product Message";
        const modalMessage = ref("");

        const showMessage = (message: string) =>
        {
            modalMessage.value = message;
            isMessageVisible.value = true;
        };

        const fetchCarts = async () =>
        {
            try
            {
                const response = await axios.get<IProduct[]>(`/api/users/${props.user.uid}/cart`);
                cartItems.value = response.data; // Update the reactive variable
            } catch (error)
            {
                console.error("Error fetching products:", error);
            }
        };

        const removeFromCart = async (productId: string) =>
        {
            const response = await axios.delete(`/api/users/${props.user.uid}/cart/${productId}`);
            const updatedCart = response.data;
            showMessage(`Successfully removed item (${productId}) from cart!`);
            cartItems.value = updatedCart;
        };

        onMounted(() =>
        {
            if (props.user)
            {
                console.log(props.user);
                fetchCarts(); // Call the function when the component is mounted
            }
        });

        return {
            cartItems,
            removeFromCart,
            isMessageVisible,
            modalTitle,
            modalMessage,
            //   closeMessageModal,
        };
    },
});
</script>
